async function RenderPdfToImage(){
	const content = new Uint8Array(await file.arrayBuffer());
    const loadingTask = getDocument({ data: content });
    const pdf = await loadingTask.promise;
    const numPages = pdf.numPages;

    const imagesArray = [];
    for (let pageNum = 1; pageNum <= numPages; pageNum++) {
      const page = await pdf.getPage(pageNum);
      const viewport = page.getViewport({ scale: 1.5 });
      const canvas = document.createElement('canvas');
      const context = canvas.getContext('2d');
      canvas.height = viewport.height;
      canvas.width = viewport.width;

      const renderContext = {
        canvasContext: context,
        viewport: viewport,
      };
      
      await page.render(renderContext).promise;
      const image = canvas.toDataURL();
      imagesArray.push(image);
    }

    setImages(imagesArray);
	mergeBase64ImagesHorizontally(imagesArray,3800,600)
	  .then(mergedImageDataURL => {
		console.log('Merged image data URL:', mergedImageDataURL);
		compressImage(mergedImageDataURL,0.5)
		setmergedimages(mergedImageDataURL);
		
	  })
	  .catch(error => {
		console.error('Error merging images:', error);
	  });
		
	  };

function mergeBase64ImagesHorizontally(base64Images, maxWidth, maxHeight) {
  return new Promise((resolve, reject) => {
    // Create a new canvas
    const canvas = document.createElement('canvas');
    const ctx = canvas.getContext('2d');

    // Load the base64 images into Image objects
    const imagePromises = base64Images.map(base64 => {
      return new Promise((resolve, reject) => {
        const img = new Image();
        img.onload = () => resolve(img);
        img.onerror = reject;
        img.src = base64;
      });
    });

    // Wait for all images to load
    Promise.all(imagePromises).then((loadedImages) => {
      // Calculate the total width and maximum height
      let totalWidth = 0;
      let maxHeight = 0;
      for (const img of loadedImages) {
        totalWidth += img.width;
        maxHeight = Math.max(maxHeight, img.height);
      }

      // Calculate the scale factor to fit within the maxWidth and maxHeight
      const widthScaleFactor = maxWidth / totalWidth;
      const heightScaleFactor = maxHeight / maxHeight;
      const scaleFactor = Math.min(widthScaleFactor, heightScaleFactor, 1);

      // Set the canvas dimensions with scaling
      canvas.width = totalWidth * scaleFactor;
      canvas.height = maxHeight * scaleFactor;

      // Draw the images on the canvas with scaling
      let x = 0;
      for (const img of loadedImages) {
        const scaledWidth = img.width * scaleFactor;
        const scaledHeight = img.height * scaleFactor;
        ctx.drawImage(img, x, 0, scaledWidth, scaledHeight);
        x += scaledWidth;
      }

      // Return the merged image as a base64 data URL
      const mergedImageDataURL = canvas.toDataURL();
      resolve(mergedImageDataURL);
    }).catch(error => {
      reject(error);
    });
  });
}
	
