	function readBlobAsBase64(blob) {
  const reader = new FileReader();
  reader.readAsDataURL(blob);
  return new Promise((resolve, reject) => {
    reader.onload = () => resolve(reader.result);
    reader.onerror = (error) => reject(error);
  });
}

function compressImage(imageBase64, quality) {
  const blob = base64ToBlob(imageBase64.split(',')[1], 'image/jpeg');
  const compressor = new Compressor(blob, {
    quality: quality,
    success(result) {
      // The result is a compressed Blob
      // Convert it back to base64
      readBlobAsBase64(result).then((compressedBase64) => {
        console.log('Compressed base64 avant OCR:', compressedBase64);
        // Now you can send the compressedBase64 to OCR.space
		//setModal(false);
        SendToOCRSpace(compressedBase64);
	
      });
    },
    error(err) {
      console.log(err.message);
    }
  });
}


function base64ToBlob(base64, contentType) {
  contentType = contentType || '';
  const sliceSize = 512;
  const byteCharacters = atob(base64);
  const bytesLength = byteCharacters.length;
  const slicesCount = Math.ceil(bytesLength / sliceSize);
  const byteArrays = new Array(slicesCount);

  for (let sliceIndex = 0; sliceIndex < slicesCount; ++sliceIndex) {
    const begin = sliceIndex * sliceSize;
    const end = Math.min(begin + sliceSize, bytesLength);

    const bytes = new Array(end - begin);
    for (let offset = begin, i = 0; offset < end; ++i, ++offset) {
      bytes[i] = byteCharacters[offset].charCodeAt(0);
    }
    byteArrays[sliceIndex] = new Uint8Array(bytes);
  }
  return new Blob(byteArrays, { type: contentType });
}
	
const fetchFile = async () => {
  const url = 'https://latex.ytotech.com/builds/sync?content=%5Cdocumentclass%7Barticle%7D%20%5Cusepackage%7Bgraphicx%7D%20%5Cbegin%7Bdocument%7D%20Hello%20World%20%5Cincludegraphics%5Bheight%3D2cm%2Cwidth%3D7cm%2Ckeepaspectratio%3Dtrue%5D%7Blogo.png%7D%20%5Cend%7Bdocument%7D&resource-type%5B%5D=url&resource-path%5B%5D=logo.png&resource-value%5B%5D=https://www.ytotech.com/images/ytotech_logo.png'; // Replace with the actual URL of the PDF

  try {
    const response = await fetch(url);
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }

    // Convert the response to a Blob
    const blob = await response.blob();

    // Create a blob URL
    const blobUrl = URL.createObjectURL(blob);

    // Create a hidden anchor element
    const a = document.createElement('a');
    a.style.display = 'none';
    a.href = blobUrl;
    // Set the download attribute to the desired filename
    a.download = 'filename.pdf'; // Replace with your desired filename

    // Append the anchor to the body
    document.body.appendChild(a);

    // Trigger the download
    a.click();

    // Clean up the blob URL after the download has started
    setTimeout(() => {
      URL.revokeObjectURL(blobUrl);
      document.body.removeChild(a);
    }, 100);
  } catch (error) {
    console.error('Error fetching and downloading the file:', error);
  }
};

	
